___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary: #0A174E;
    --secondary: #F5D042;
}

body {
    min-height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    background-color: var(--secondary);
    color: var(--secondary);
    overflow-x: hidden;
}

img {
    width: clamp(100px, 80%, 700px);

}

header,
footer,
#content {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--primary);
}

header {
    padding: 20px;
    gap: 10px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

footer {
    height: 60px;
    width: 100%;
    margin-top: auto;
}

#content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 80%;
    border-radius: 20px;
    padding: 30px;
}

.gridContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 20px;
    padding: 20px;
}

.menuCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    background-color: var(--secondary);
    color: var(--primary);
    border-radius: 15px;

}

button {
    background-color: var(--secondary);
    color: var(--primary);
    width: 100px;
    height: 30px;
    border: none;
    border-radius: 10px;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,iBAAiB;IACjB,WAAW;IACX,SAAS;IACT,UAAU;IACV,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,kCAAkC;IAClC,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,+BAA+B;;AAEnC;;AAEA;;;IAGI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,gCAAgC;AACpC;;AAEA;IACI,aAAa;IACb,SAAS;IACT,WAAW;IACX,aAAa;IACb,eAAe;IACf,6BAA6B;AACjC;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,cAAc;IACd,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,cAAc;IACd,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;IACb,kCAAkC;IAClC,qBAAqB;IACrB,mBAAmB;;AAEvB;;AAEA;IACI,kCAAkC;IAClC,qBAAqB;IACrB,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,mBAAmB;AACvB","sourcesContent":[":root {\n    --primary: #0A174E;\n    --secondary: #F5D042;\n}\n\nbody {\n    min-height: 100vh;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 50px;\n    background-color: var(--secondary);\n    color: var(--secondary);\n    overflow-x: hidden;\n}\n\nimg {\n    width: clamp(100px, 80%, 700px);\n\n}\n\nheader,\nfooter,\n#content {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: var(--primary);\n}\n\nheader {\n    padding: 20px;\n    gap: 10px;\n    width: 100%;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-around;\n}\n\nfooter {\n    height: 60px;\n    width: 100%;\n    margin-top: auto;\n}\n\n#content {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    max-width: 80%;\n    border-radius: 20px;\n    padding: 30px;\n}\n\n.gridContainer {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n    grid-gap: 20px;\n    padding: 20px;\n}\n\n.menuCard {\n    display: flex;\n    flex-direction: column;\n    align-it